/* Example CSS for frndPop.css. Replace or extend as needed. */

.friendPopup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.frnd-content {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 32px rgba(0, 0, 0, 0.15);
  width: 400px;
  max-width: 95vw;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.frnd-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 24px 0 24px;
}

.title-wrap .title {
  font-size: 1.3rem;
  font-weight: bold;
  color: #222;
}

.title-wrap .subtitle {
  font-size: 0.95rem;
  color: #888;
  display: block;
  margin-top: 2px;
}

.icon-btn {
  background: none;
  border: none;
  font-size: 1.2rem;
  cursor: pointer;
  color: #888;
  transition: color 0.2s;
}

.icon-btn:hover {
  color: #e74c3c;
}

.frnd-body {
  padding: 24px;
  flex: 1;
}

.field {
  margin-bottom: 18px;
}

.field-label {
  display: block;
  font-size: 0.98rem;
  color: #444;
  margin-bottom: 6px;
}

.chips-wrap {
  margin-bottom: 4px;
}

.hint {
  font-size: 0.85rem;
  color: #888;
  margin-top: 2px;
}

.input-group {
  position: relative;
  display: flex;
  align-items: center;
}

.input-icon {
  position: absolute;
  left: 10px;
  color: #bbb;
  font-size: 1rem;
}

.input {
  width: 100%;
  padding: 8px 8px 8px 32px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
  outline: none;
  transition: border 0.2s;
}

.input:focus {
  border-color: #007bff;
}

.grid-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

.split-preview {
  display: flex;
  align-items: center;
  background: #f6f8fa;
  color: #007bff;
  border-radius: 4px;
  padding: 8px 12px;
  font-size: 0.98rem;
  margin-bottom: 12px;
  margin-top: 8px;
  gap: 8px;
}

.alert {
  display: flex;
  align-items: center;
  padding: 8px 12px;
  border-radius: 4px;
  font-size: 0.97rem;
  margin-bottom: 10px;
  gap: 8px;
}

.alert-error {
  background: #ffeaea;
  color: #d32f2f;
}

.alert-success {
  background: #eaffea;
  color: #388e3c;
}

.frnd-footer {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  padding: 16px 24px 24px 24px;
  border-top: 1px solid #f0f0f0;
}

.btn {
  padding: 8px 18px;
  border-radius: 4px;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
}

.btn-ghost {
  background: none;
  color: #007bff;
  border: 1px solid #007bff;
}

.btn-ghost:hover {
  background: #f0f8ff;
}

.btn-primary {
  background: #007bff;
  color: #fff;
}

.btn-primary:disabled {
  background: #b3d7ff;
  color: #fff;
  cursor: not-allowed;
}

.btn-flex {
  display: flex;
  align-items: center;
  gap: 8px;
}

.spinner {
  width: 16px;
  height: 16px;
  border: 2px solid #fff;
  border-top: 2px solid #007bff;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
  display: inline-block;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
