.signup {
  display: flex;

  margin-top: 8%;
  align-items: center;
  justify-content: center;
}

.signup-logo {
  margin-right: 100px;
  height: 300px;
}

.signup-form h3 {
  font-size: 15px;
  color: #99a4c1;
}
.signup-form label {
  font-size: 22px;
}
.signup-form input {
  font-size: 25px;
  margin-bottom: 10px;
}

.signup-form .btn {
  /* padding: 25px; */
  margin-top: 10px;
  font-size: 20px;
  background: #ff652f;
  color: white;
}

/* Adds space only when a button follows another button */
.signup-form .btn + .btn {
  margin-left: 12px; /* change 12px to any spacing you like */
}

/* Put these at the bottom of signup.css */

/* Password show/hide wrapper */
.password-field {
  position: relative;
}

/* Give room for the toggle button inside the input */
.password-field .form-control {
  padding-right: 84px; /* adjust if needed */
}

/* Toggle button (inside the input, on the right) */
.password-field .toggle {
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  background: transparent;
  border: 0;
  cursor: pointer;
  font-size: 0.9rem;
  padding: 6px 10px;
  color: #6b7280; /* neutral gray */
  border-radius: 8px;
}

.password-field .toggle:hover {
  background: rgba(0, 0, 0, 0.05);
}

.password-field .toggle:focus {
  outline: 2px solid #ff652f; /* your theme accent */
  outline-offset: 2px;
}

/* Optional secondary button style */
.btn.btn-secondary {
  background: #e5e7eb; /* light gray */
  color: #111827; /* dark text */
}
.btn.btn-secondary:hover {
  filter: brightness(0.97);
}
