.DashboardNav {
  height: 60px;
  padding: 0 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(15, 23, 42, 0.55);
  border-bottom: 1px solid rgba(255, 255, 255, 0.14);
  color: #e5e7eb;
  backdrop-filter: saturate(180%) blur(14px);
  -webkit-backdrop-filter: saturate(180%) blur(14px);
  z-index: 1000;
}

.nav-brand {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  color: inherit;
  text-decoration: none;
}
.brand-logo {
  width: 34px;
  height: 34px;
  border-radius: 999px;
  display: grid;
  place-items: center;
  background: linear-gradient(135deg, #6366f1, #8b5cf6);
  color: #fff;
  font-weight: 800;
  box-shadow: 0 10px 20px rgba(99, 102, 241, 0.3);
}
.brand-text {
  display: flex;
  flex-direction: column;
  line-height: 1.1;
}
.brand-title {
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 0.2px;
}
.brand-sub {
  font-size: 11px;
  color: #a3b0c2;
}

.nav-right {
  display: inline-flex;
  align-items: center;
  gap: 10px;
}
.user-chip {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 4px 8px;
  border-radius: 999px;
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.14);
}
.profile {
  width: 22px;
  height: 22px;
  border-radius: 999px;
}
.user-name {
  font-size: 12px;
  color: #e5e7eb;
}

.logoutbtn {
  height: 34px;
  padding: 0 12px;
  border-radius: 10px;
  border: 1px solid transparent;
  color: #fff;
  background: linear-gradient(135deg, #ef4444, #f97316);
  cursor: pointer;
  font-weight: 600;
}
.logoutbtn:hover {
  opacity: 0.95;
}
