/* ========= Modern Dashboard Theme (glass + soft gradients) ========= */

:root {
  --bg: #0b1220; /* deep slate */
  --card: rgba(255, 255, 255, 0.08);
  --border: rgba(255, 255, 255, 0.14);
  --text: #e5e7eb;
  --muted: #a3b0c2;
  --brand: #6366f1; /* indigo-500 */
  --brand2: #22c55e; /* green-500 */
  --danger: #ef4444;
  --ring: rgba(99, 102, 241, 0.35);
  --shadow: 0 20px 40px rgba(0, 0, 0, 0.35);

  --radius-xl: 22px;
  --radius-lg: 16px;
  --radius-md: 12px;
  --radius-sm: 10px;

  --pad-lg: 20px;
  --pad-md: 14px;
  --pad-sm: 10px;

  --blur: saturate(180%) blur(18px);
}

html,
body,
#root {
  height: 100%;
}

.dash {
  min-height: 100%;
  background: radial-gradient(
      1200px 700px at 80% -10%,
      rgba(99, 102, 241, 0.18),
      transparent 60%
    ),
    radial-gradient(
      900px 600px at -10% 10%,
      rgba(34, 197, 94, 0.12),
      transparent 60%
    ),
    var(--bg);
  color: var(--text);
  padding: 18px;
}

/* Header */
.dash-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  margin-bottom: 16px;
}

.brand {
  display: flex;
  align-items: center;
  gap: 12px;
}
.logo-circle {
  width: 40px;
  height: 40px;
  display: grid;
  place-items: center;
  border-radius: 999px;
  background: linear-gradient(135deg, var(--brand), #8b5cf6);
  color: white;
  font-weight: 800;
  box-shadow: 0 8px 18px rgba(99, 102, 241, 0.35);
}
.brand-title {
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.2px;
}
.brand-sub {
  font-size: 12px;
  color: var(--muted);
}

.header-actions {
  display: flex;
  gap: 10px;
}

/* Buttons */
.btn-primary,
.btn-outline {
  height: 40px;
  padding: 0 14px;
  border-radius: 12px;
  border: 1px solid transparent;
  font-weight: 600;
  color: white;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  transition: transform 0.05s ease, box-shadow 0.2s ease, opacity 0.2s ease;
}
.btn-primary {
  background: linear-gradient(135deg, var(--brand), #8b5cf6);
  box-shadow: 0 12px 24px rgba(99, 102, 241, 0.25);
}
.btn-primary:hover {
  transform: translateY(-1px);
  box-shadow: 0 16px 30px rgba(99, 102, 241, 0.35);
}

.btn-outline {
  color: var(--text);
  background: rgba(255, 255, 255, 0.08);
  border-color: var(--border);
}
.btn-outline:hover {
  background: rgba(255, 255, 255, 0.12);
}

/* Stat grid */
.stat-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
  margin-bottom: 16px;
}

.stat-card {
  border: 1px solid var(--border);
  background: var(--card);
  backdrop-filter: var(--blur);
  -webkit-backdrop-filter: var(--blur);
  border-radius: var(--radius-xl);
  padding: 18px;
  box-shadow: var(--shadow);
}
.stat-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--muted);
  font-size: 12px;
}
.stat-label {
  font-weight: 600;
  letter-spacing: 0.2px;
}
.stat-value {
  font-size: 28px;
  font-weight: 800;
  margin-top: 8px;
}
.stat-sub {
  color: var(--muted);
  font-size: 12px;
  margin-top: 4px;
}

/* Main content grid */
.content-grid {
  display: grid;
  grid-template-columns: 1.7fr 0.9fr;
  gap: 12px;
}

/* Panels */
.panel {
  border: 1px solid var(--border);
  background: var(--card);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow);
  overflow: hidden;
}

.panel-side {
  min-height: 320px;
}

.panel-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 16px;
  border-bottom: 1px solid var(--border);
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.06), transparent);
}
.panel-title {
  font-weight: 700;
  letter-spacing: 0.2px;
  display: inline-flex;
  gap: 8px;
  align-items: center;
}
.panel-actions {
  display: inline-flex;
  gap: 10px;
  align-items: center;
}

.search {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  border: 1px solid var(--border);
  background: rgba(255, 255, 255, 0.08);
  padding: 0 10px;
  height: 36px;
  border-radius: 10px;
}
.search-input {
  background: transparent;
  border: none;
  outline: none;
  color: var(--text);
}

/* Expense list */
.exp-list {
  padding: 10px;
}
.exp-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  padding: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.05);
  border-radius: 14px;
  margin-bottom: 8px;
}
.exp-row:hover {
  background: rgba(255, 255, 255, 0.08);
}

.exp-main {
  min-width: 0;
}
.exp-desc {
  font-weight: 600;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.exp-meta {
  margin-top: 6px;
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.pill {
  padding: 4px 8px;
  font-size: 12px;
  border-radius: 999px;
  border: 1px solid var(--border);
  color: var(--muted);
  background: rgba(255, 255, 255, 0.06);
}
.pill-ghost {
  background: transparent;
}

.exp-right {
  text-align: right;
}
.exp-amount {
  font-weight: 800;
}

/* Empty states */
.empty {
  text-align: center;
  padding: 36px 16px;
}
.empty.small {
  padding: 24px 16px;
}
.empty-emoji {
  font-size: 30px;
}
.empty-title {
  font-weight: 800;
  margin-top: 8px;
}
.empty-sub {
  color: var(--muted);
  font-size: 13px;
  margin-top: 4px;
}

/* Friends list */
.friend-ul {
  list-style: none;
  margin: 0;
  padding: 10px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 8px;
}
.friend-li {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.avatar {
  width: 34px;
  height: 34px;
  border-radius: 999px;
  display: grid;
  place-items: center;
  background: linear-gradient(135deg, var(--brand2), #16a34a);
  color: white;
  font-weight: 800;
  box-shadow: 0 10px 18px rgba(34, 197, 94, 0.25);
}
.friend-name {
  font-weight: 600;
}

/* Mobile FAB */
.fab {
  position: fixed;
  right: 18px;
  bottom: 18px;
  width: 54px;
  height: 54px;
  border-radius: 999px;
  background: linear-gradient(135deg, var(--brand), #8b5cf6);
  color: white;
  border: none;
  cursor: pointer;
  display: none; /* shown on mobile */
  box-shadow: 0 14px 28px rgba(99, 102, 241, 0.35);
  transition: transform 0.05s ease;
}
.fab:hover {
  transform: translateY(-1px);
}

/* Responsive */
@media (max-width: 980px) {
  .stat-grid {
    grid-template-columns: 1fr;
  }
  .content-grid {
    grid-template-columns: 1fr;
  }
  .fab {
    display: grid;
    place-items: center;
  }
}

/* Make room below the fixed site header, if needed */
.dash {
  padding-top: 76px;
} /* adjust if your header height differs */

/* Header buttons */
.dash-header .header-actions {
  display: flex;
  gap: 10px;
  align-items: center;
}

/* Tiny icon button used in Friends panel */
.btn-mini {
  height: 34px;
  width: 34px;
  display: grid;
  place-items: center;
  border-radius: 10px;
  border: 1px solid var(--border);
  background: rgba(255, 255, 255, 0.08);
  color: var(--text);
  cursor: pointer;
}
.btn-mini:hover {
  background: rgba(255, 255, 255, 0.12);
}

/* Delete button (right side of each expense row) */
.btn-delete {
  margin-left: 10px;
  height: 34px;
  width: 38px;
  display: grid;
  place-items: center;
  border-radius: 10px;
  border: 1px solid var(--border);
  background: rgba(255, 255, 255, 0.08);
  color: var(--text);
  cursor: pointer;
}
.btn-delete:hover {
  background: rgba(255, 255, 255, 0.12);
}

/* tiny spinner variant used inside delete button */
.spinner.tiny {
  width: 14px;
  height: 14px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top-color: rgba(255, 255, 255, 0.9);
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
